<template>
	<section id="ohmybox-component">
		<div
			class="ohmybox-gesture"
			ref="ohmyboxContainter"
			@touchstart.stop="touchStart"
			@touchmove.stop="touchMove"
			@touchend.stop="touchEnd"
			@touchcancel.stop="touchEnd"
			:style="{ webkitTransform: 'translate3d(0,' + currentPosition + 'px,0)' }"
		>
			<div class="ohmybox-item-wrap">
				<slot></slot>
			</div>
		</div>
	</section>
</template>
<script>
export default {
	name:'ohmybox',
	props: {
		//    px
		scrollHeight: {
			default: function() {
				return 300
			}
		},
		// 
		quickScrollHeight: {
			default: function() {
				return 20
			}
		},
		// 
		clickDuration:{
			default: function() {
				return 10
			}
		},
		// 
		clickDistance:{
			default: function() {
				return 30
			}
		},
		//      
		scrollDuration: {
			default: function() {
				return 400
			}
		},
		// item 
		itemHeight: {
			default: function() {
				return 0
			}
		},
		change: {
			default: ()=>{}
		},
		click: {
			default: ()=>{}
		}
	},
	data: () => {
		return {
			startTime: 0, // 
			page: 0, // item
			lastPage: null,
			defaultItemHeight: window.innerHeight, // item
			initialPosition: 0, //  touchStart 
			currentPosition: 0, // touchMove 
			startY: 0, // touchstart y
			direction: ''
		}
	},
	created() {
		this.resetOffsetHandle()
	},
	mounted() {
		window.addEventListener("resize",this.onresize);
	},
	activated() {
		this.resetOffsetHandle()
	},
	methods: {
		touchStart(e) {
			this.$refs.ohmyboxContainter.classList.remove('trans')
			this.startTime = new Date().getTime()
			this.startY = e.touches[0].pageY
			this.initialPosition = this.currentPosition // 
		},
		touchMove(e) {
			let deltaY = e.touches[0].pageY - this.startY
			let translate = this.initialPosition + deltaY
			this.currentPosition = translate
		},
		touchEnd(e) {
			this.$refs.ohmyboxContainter.classList.add('trans')
			let t = e.changedTouches[0].pageY
			let endTime = new Date().getTime()
			if (endTime - this.startTime < this.clickDuration && t - this.startY < this.clickDistance) {
				// console.log('')
				let itemLength = document.querySelector('.ohmybox-item-wrap').children.length;
				this.$emit('click', this.page, itemLength, this._self)
				return;
			}
			if (endTime - this.startTime < this.scrollDuration) {
				if (t - this.startY > this.quickScrollHeight) {
					this.prevHandle()
				} else if (this.startY - t > this.quickScrollHeight) {
					this.nextHandle()
				} else {
					this.resetOffsetHandle()
					// console.log(' ')
				}
			} else {
				let offset = t - this.startY
				if (offset > this.scrollHeight) {
					this.prevHandle()
				} else if (-offset > this.scrollHeight) {
					this.nextHandle()
				} else {
					this.resetOffsetHandle()
				}
			}
		},
		// 
		nextHandle() {
			this.direction = 'next';
			let itemLength = document.querySelector('.ohmybox-item-wrap').children.length;
			if(itemLength == this.page + 1){
				// 
				this.lastPage = this.page
				this.resetOffsetHandle()
				this.$emit('change', itemLength, itemLength, this._self, this.lastPage, this.direction)
				this.$emit('toTop')
			}else{
				this.lastPage = this.page
				this.page++
				this.resetOffsetHandle()
				this.$emit('change', this.page, itemLength, this._self, this.lastPage, this.direction)
			}
		},
		// 
		prevHandle() {
			this.direction = 'prev';
			let itemLength = document.querySelector('.ohmybox-item-wrap').children.length;
			if (this.page <= 0) {
				// 
				this.lastPage = this.page
				this.page = 0
				this.$emit('change', this.page, itemLength, this._self, this.lastPage, this.direction)
				this.$emit('toBottom')
				this.resetOffsetHandle()
			}else{
				this.lastPage = this.page
				this.page--
				this.resetOffsetHandle()
				this.$emit('change', this.page, itemLength, this._self, this.lastPage, this.direction)
			}
		},
		// 
		resetOffsetHandle() {
			this.currentPosition = -this.page * (this.itemHeight || this.defaultItemHeight)
		},
		// item
		onresize() {
			this.defaultItemHeight = window.innerHeight;
		},
		// item
		forceScrollHandle(idx) {
			if(idx) {
				this.$refs.ohmyboxContainter.classList.add('trans')
				// setTimeout
				setTimeout(()=> {
					this.page = idx;
					this.currentPosition = -this.page * (this.itemHeight || this.defaultItemHeight)
				}, 0)
				
			}
		}
	}
}
</script>
<style scoped>
#ohmybox-component {
	position: fixed;
	overflow: hidden;
	width:100vw;
	height:100vh;
}
.ohmybox-gesture {
	-webkit-transform: translate3d(0, 0, 0);
	-webkit-backface-visibility: hidden;
	-webkit-perspective: 1000;
	will-change: --webkit-transform;
}
.trans {
	-webkit-transition: all 0.3s ease-out;
}
</style>
